import React, { useState } from 'react'; // useState 
import { 
  Card, 
  CardContent, 
  CardMedia, 
  Typography, 
  Chip, 
  Box, 
  Rating, 
  IconButton, 
  CardActions, 
  Menu, // 
  MenuItem // 
} from '@mui/material';
import { Edit as EditIcon, Delete as DeleteIcon, Refresh as RefreshIcon, PlaylistAdd as PlaylistAddIcon } from '@mui/icons-material'; // PlaylistAddIcon 
import { Anime, Genre } from '../types';
import { useCustomLists } from '../context/CustomListContext'; // 

// 
const genreColors: Record<Genre, string> = {
  '': '#f44336',
  '': '#ff9800',
  '': '#9c27b0',
  '': '#2196f3',
  'SF': '#673ab7',
  '': '#4caf50',
  '': '#e91e63',
  '': '#00bcd4',
  '': '#607d8b',
  '': '#795548',
  '': '#9e9e9e'
};

interface AnimeCardProps {
  anime: Anime;
  onEdit: (anime: Anime) => void;
  onDelete: (id: string) => void;
  onRewatch: (id: string) => void;
}

const AnimeCard: React.FC<AnimeCardProps> = ({ anime, onEdit, onDelete, onRewatch }) => {
  const { customLists, addAnimeToCustomList } = useCustomLists(); // 
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null); // 
  const open = Boolean(anchorEl); // 
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('ja-JP', { year: 'numeric', month: 'long', day: 'numeric' });
  };

  // : 
  const handleMenuClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleAddToList = (listId: string) => {
    addAnimeToCustomList(listId, anime.id);
    handleMenuClose();
    // : Snackbar
  };
  // ---  ---

  return (
    <Card sx={{ 
      height: '100%', 
      display: 'flex', 
      flexDirection: 'column',
      transition: 'transform 0.2s',
      '&:hover': {
        transform: 'scale(1.02)'
      }
    }}>
      <CardMedia
        component="img"
        height="200"
        image={anime.coverImage || 'https://via.placeholder.com/300x200?text=No+Image'}
        alt={anime.title}
        sx={{ objectFit: 'cover' }}
      />
      <CardContent sx={{ flexGrow: 1 }}>
        <Typography gutterBottom variant="h5" component="div" sx={{ fontWeight: 'bold' }}>
          {anime.title}
        </Typography>
        
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
          <Rating value={anime.personalRating} readOnly precision={0.5} size="small" />
          <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
            ({anime.personalRating})
          </Typography>
        </Box>
        
        <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
          {anime.year ? `${anime.year}` : ''}
        </Typography>
        
        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
          : {formatDate(anime.watchedDate)}
        </Typography>
        
        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mb: 2 }}>
          {anime.genres.map((genre) => (
            <Chip 
              key={genre} 
              label={genre} 
              size="small" 
              sx={{ 
                backgroundColor: genreColors[genre],
                color: 'white',
                fontWeight: 500
              }} 
            />
          ))}
        </Box>
        
        {anime.notes && (
          <Typography variant="body2" color="text.secondary">
            {anime.notes}
          </Typography>
        )}
      </CardContent>
      
      <CardActions sx={{ justifyContent: 'flex-end', p: 1 }}>
        {/* :  */}
        <IconButton size="small" onClick={handleMenuClick} title="">
          <PlaylistAddIcon />
        </IconButton>
        <Menu
          anchorEl={anchorEl}
          open={open}
          onClose={handleMenuClose}
          MenuListProps={{
            'aria-labelledby': 'add-to-list-button',
          }}
        >
          {customLists.length > 0 ? (
            customLists.map((list) => (
              <MenuItem key={list.id} onClick={() => handleAddToList(list.id)}>
                {list.name}
              </MenuItem>
            ))
          ) : (
            <MenuItem disabled></MenuItem>
          )}
        </Menu>
        {/* ---  --- */}
        <IconButton size="small" onClick={() => onRewatch(anime.id)} title="">
          <RefreshIcon />
        </IconButton>
        <IconButton size="small" onClick={() => onEdit(anime)} title="">
          <EditIcon />
        </IconButton>
        <IconButton size="small" onClick={() => onDelete(anime.id)} title="">
          <DeleteIcon />
        </IconButton>
      </CardActions>
    </Card>
  );
};

export default AnimeCard;
