import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Box,
  Alert,
  Typography,
  Tabs,
  Tab
} from '@mui/material';
import { Download as DownloadIcon, Upload as UploadIcon } from '@mui/icons-material';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`import-export-tabpanel-${index}`}
      aria-labelledby={`import-export-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ pt: 2 }}>{children}</Box>}
    </div>
  );
}

interface ImportExportDialogProps {
  open: boolean;
  onClose: () => void;
  onExport: () => string;
  onImport: (data: string) => boolean;
}

const ImportExportDialog: React.FC<ImportExportDialogProps> = ({
  open,
  onClose,
  onExport,
  onImport
}) => {
  const [tabValue, setTabValue] = useState(0);
  const [importData, setImportData] = useState('');
  const [importError, setImportError] = useState<string | null>(null);
  const [importSuccess, setImportSuccess] = useState(false);

  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
    setImportData('');
    setImportError(null);
    setImportSuccess(false);
  };

  const handleExport = () => {
    const data = onExport();
    const blob = new Blob([data], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `anime_archive_export_${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleImport = () => {
    try {
      const success = onImport(importData);
      if (success) {
        setImportSuccess(true);
        setImportError(null);
        setImportData('');
      } else {
        setImportError('');
        setImportSuccess(false);
      }
    } catch (e) {
      setImportError('JSON');
      setImportSuccess(false);
    }
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>/</DialogTitle>
      <Box sx={{ borderBottom: 1, borderColor: 'divider', px: 3 }}>
        <Tabs value={tabValue} onChange={handleTabChange} aria-label="import export tabs">
          <Tab icon={<DownloadIcon />} label="" id="import-export-tab-0" />
          <Tab icon={<UploadIcon />} label="" id="import-export-tab-1" />
        </Tabs>
      </Box>
      <DialogContent>
        <TabPanel value={tabValue} index={0}>
          <Typography variant="body1" gutterBottom>
            JSON
          </Typography>
          <Box sx={{ mt: 2, display: 'flex', justifyContent: 'center' }}>
            <Button
              variant="contained"
              color="primary"
              startIcon={<DownloadIcon />}
              onClick={handleExport}
            >
              JSON
            </Button>
          </Box>
        </TabPanel>
        <TabPanel value={tabValue} index={1}>
          <Typography variant="body1" gutterBottom>
            JSON
          </Typography>
          <TextField
            label="JSON"
            multiline
            rows={10}
            value={importData}
            onChange={(e) => setImportData(e.target.value)}
            fullWidth
            margin="normal"
            variant="outlined"
            error={!!importError}
            helperText={importError}
          />
          {importSuccess && (
            <Alert severity="success" sx={{ mt: 2 }}>
              
            </Alert>
          )}
        </TabPanel>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}></Button>
        {tabValue === 1 && (
          <Button
            onClick={handleImport}
            variant="contained"
            color="primary"
            disabled={!importData}
          >
            
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
};

export default ImportExportDialog;
